<script>
export default {
  name: "ThirdNav",
  data() {
    return {
      index: -1,
      sideVal: [
        "00",
        "01",
        "02",
        "03",
        "04",
        "05",
        "06",
        "07",
        "08",
        "09",
        "10",
        "11",
        "12",
        "13",
        "14",
        "15",
        "16",
        "17",
      ],
      isShow: false,
    };
  },
  methods: {
    changeN(e) {
      this.isShow = true;
      let a = e.target;
      if (e.target.children.length === 0) {
        a = e.target.parentNode;
      }
      let arr = [];
      arr.slice
        .call(this.$refs.nav.childNodes[0].childNodes)
        .forEach((el, index) => {
          if (el === a) {
            this.index = index;
            a.style.background = "#ccc";
          }
        });
    },
    remakeN(e) {
      this.index = -1;
      this.isShow = false;
      let a = e.target;
      if (e.target.children.length === 0) {
        a = e.target.parentNode;
      }
      a.style.background = "";
    },
    mainShow(i) {
      if (i === this.index) {
        return true;
      } else {
        return false;
      }
    },
    handleScroll() {
      let t = document.documentElement.scrollTop;
      let ot = this.$refs.nav.offsetTop;
      let oh = this.$refs.nav.offsetHeight;
      if (t > ot && t < oh + ot) {
        this.$refs.navside.style.top = t - ot + "px";
      } else {
        this.$refs.navside.style.top = "10px";
      }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

