<template>
  <div class="box-gg" v-if="isShow">
    <div class="box-gg-center w">
      <p class="ggclose" @click="removeP">×</p>
      <img src="../assets/img/jdgg.webp" alt="" />
    </div>
  </div>
</template>

<script>
export default {
  name: "Boxgg",
  data() {
    return {
      isShow: true,
    };
  },
  methods: {
    removeP() {
      this.isShow = false;
      this.$store.state.sekillTop -= 80;
      this.$store.state.dailysaleTop -= 80;
      this.$store.state.squareTop -= 80;
      this.$store.state.foryouTop -= 80;
    },
  },
};
</script>

<style lang="less">
.box-gg {
  background-color: rgb(199, 41, 66);
  height: 80px;
  position: relative;
  img {
    margin: 0 auto;
  }
  .ggclose {
    margin: 0;
    position: absolute;
    right: 172px;
    top: 0;
    width: 20px;
    height: 20px;
    background-color: rgba(0, 0, 0, 0.3);
    line-height: 20px;
    text-align: center;
    font-size: 24px;
    color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    z-index: 9;
  }
}
</style>
