<template>
  <div class="top-map">
    <a href="javascript:;"
      ><i class="fas fa-map-marker-alt"></i><span>{{ city }}</span></a
    >
    <div class="top-map1">
      <div class="map-main clearfix">
        <ul>
          <li
            v-for="(m, index) in main"
            :key="index"
            @click="changeCity(index)"
          >
            <span ref="m">{{ m }}</span>
          </li>
        </ul>
      </div>
      <div class="dq clearfix">
        <h5>地区专享版本</h5>
        <ul>
          <li>中国港澳</li>
          <li>中国台湾</li>
          <li>京东全球</li>
        </ul>
      </div>
      <div class="as clearfix">
        <h5>Available Sites</h5>
        <ul>
          <li v-for="(a, index) in as" :key="index">
            <i :style="`background-position:${a.position}`"></i>{{ a.val }}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TopMap",
  data() {
    return {
      as: [
        { val: "Global Site", position: "0px 0px" },
        { val: "Сайт России", position: "-20px 0px" },
        { val: "Situs Indonesia", position: "17px 0px" },
        { val: "Sitio de España", position: "0px 15px" },
        { val: "เว็บไซต์ประเทศไทย", position: "-17px 15px" },
      ],
      main: [
        "北京",
        "上海",
        "天津",
        "重庆",
        "河北",
        "山西",
        "河南",
        "辽宁",
        "吉林",
        "黑龙江",
        "内蒙古",
        "江苏",
        "山东",
        "安徽",
        "浙江",
        "福建",
        "湖北",
        "湖南",
        "广东",
        "广西",
        "江西",
        "四川",
        "海南",
        "贵州",
        "云南",
        "西藏",
        "陕西",
        "甘肃",
        "青海",
        "宁夏",
        "新疆",
        "港澳",
        "台湾",
        "钓鱼岛",
        "海外",
      ],
      city: "浙江",
    };
  },
  methods: {
    changeCity(index) {
      this.city = this.main[index];
      this.$refs.m.map((item) => (item.className = ""));
      this.$refs.m[index].className = "current";
    },
  },
};
</script>

<style lang="less" scoped>
.top-map {
  position: relative;
  display: flex;
  height: 100%;
  padding: 0px 5px;
  border-left: solid 1px #e3e4e5;
  border-right: 1px solid #e3e4e5;
  a {
    height: 100%;
    display: block;
    display: flex;
    align-items: center;
    i {
      color: #f10215;
      margin-right: 4px;
    }
    span {
      color: #999;
    }
  }
  a:hover span{
    color: #f10215;
  }
  .top-map1 {
    width: 330px;
    background-color: #fff;
    position: absolute;
    top: 31px;
    left: -1px;
    z-index: 10;
    border: 1px solid #e3e4e5;
    border-top: none;
    display: none;
    padding: 15px 0;
    box-sizing: border-box;
    .map-main {
      margin: 5px 10px;
      ul {
        li {
          float: left;
          width: 50px;
          box-sizing: border-box;
          margin-right: 10px;
          margin-bottom: 5px;
          cursor: pointer;
          span {
            padding: 2px 6px;
            line-height: 2;
          }
        }
      }
    }
    .dq {
      margin: 0 20px;
      border-top: 1px solid #f1f2f3;
      border-bottom: 1px solid #f1f2f3;
      h5 {
        line-height: 4;
      }
      ul {
        width: 260px;
        li {
          float: left;
          width: 130px;
          margin-bottom: 10px;
          cursor: pointer;
          i {
            display: inline-block;
            height: 12px;
            width: 17px;
            margin-right: 5px;
            background-image: url("../assets/img/mapimg.png");
          }
        }
        li:hover {
          color: #f10215;
        }
      }
    }
    .as {
      margin: 0 20px;
      height: auto;
      h5 {
        line-height: 4;
      }
      ul {
        width: 260px;
        li {
          float: left;
          width: 130px;
          margin-bottom: 10px;
          cursor: pointer;
          i {
            display: inline-block;
            height: 12px;
            width: 17px;
            margin-right: 5px;
            background-image: url("../assets/img/mapimg.png");
          }
        }
        li:hover {
          color: #f10215;
        }
      }
    }
  }
}
// 设置顶部定位下拉框
.top-map:hover .top-map1 {
  display: block;
}
.top-map:hover {
  background-color: #fff;
  border-color: #ddd;
}
.current {
  background: #f10215;
  color: #fff;
}
</style>
