<template>
  <div class="rightnavbar" ref="rightnav">
    <ul>
      <li
        v-for="(l, index) in lis"
        :key="index"
        @mouseenter="changeB(index)"
        @mouseleave="removeB(index)"
        ref="lis"
        @click="goBar(index)"
      >
        {{ l }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Rightnavbar",
  data() {
    return {
      lis: [
        "京东秒杀",
        "特色优选",
        "频道广场",
        "为你推荐",
        "客服",
        "反馈",
        "顶部",
      ],
    };
  },
  methods: {
    changeB(index) {
      this.$refs.lis[index].className = "navliclass";
    },
    removeB(index) {
      this.$refs.lis[index].className = "";
    },
    handleScroll() {
      let domTop = document.documentElement.scrollTop;
      if (domTop >= this.$store.state.sekillTop - 40) {
        this.$refs.rightnav.style.position = "fixed";
        this.$refs.rightnav.style.top = "80px";
        this.$refs.lis[this.$refs.lis.length - 1].style.display = "block";
      } else {
        this.$refs.rightnav.style.position = "absolute";
        this.$refs.lis[this.$refs.lis.length - 1].style.display = "";
        this.$refs.rightnav.style.top = this.$store.state.sekillTop + "px";
      }
      if (
        domTop >= this.$store.state.sekillTop - 50 &&
        domTop <= this.$store.state.dailysaleTop - 60
      ) {
        for (let i = 0; i < 4; i++) {
          this.$refs.lis[i].style.color = "";
        }
        this.$refs.lis[0].style.color = "#c81623";
      } else if (
        domTop >= this.$store.state.dailysaleTop - 60 &&
        domTop <= this.$store.state.squareTop - 80
      ) {
        for (let i = 0; i < 4; i++) {
          this.$refs.lis[i].style.color = "";
        }
        this.$refs.lis[1].style.color = "#c81623";
      } else if (
        domTop >= this.$store.state.squareTop - 90 &&
        domTop <= this.$store.state.foryouTop - 75
      ) {
        for (let i = 0; i < 4; i++) {
          this.$refs.lis[i].style.color = "";
        }
        this.$refs.lis[2].style.color = "#c81623";
      } else if (domTop >= this.$store.state.foryouTop - 75) {
        for (let i = 0; i < 4; i++) {
          this.$refs.lis[i].style.color = "";
        }
        this.$refs.lis[3].style.color = "#c81623";
      } else {
        for (let i = 0; i < 4; i++) {
          this.$refs.lis[i].style.color = "";
        }
      }
    },
    goBar(index) {
      if (index === 0) {
        this.anii(window, this.$store.state.sekillTop - 40);
      } else if (index === 1) {
        this.anii(window, this.$store.state.dailysaleTop - 50);
      } else if (index === 2) {
        this.anii(window, this.$store.state.squareTop - 70);
      } else if (index === 3) {
        this.anii(window, this.$store.state.foryouTop - 70);
        this.$refs.lis[this.$refs.lis.length - 1].style.display = "block";
        this.$refs.lis[this.$refs.lis.length - 1].style.display = "block";
      } else if (index === this.$refs.lis.length - 1) {
        this.anii(window, 0);
      }
    },
    anii(obj, target, callback) {
      //调用函数前先解除定时器 防止多次点击开多个定时器
      clearInterval(obj.timerx);
      obj.timerx = setInterval(() => {
        let winY = window.pageYOffset;
        let step = (target - winY) / 10;
        winY = Math.ceil(winY);
        let cha = winY - target;
        //给移动距离取整 防止到不了指定位置
        step = step > 0 ? Math.ceil(step) : Math.floor(step);
        //到达指定位置后，停止计时器，如果有回调函数就执行它
        if (Math.abs(cha) <= Math.abs(step)) {
          clearInterval(obj.timerx);
          callback && callback();
        }
        window.scroll(0, winY + step);
      }, 15);
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    window.addEventListener("scroll", this.goBar);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
    window.removeEventListener("scroll", this.goBar);
  },
};
</script>

<style lang="less">
.rightnavbar {
  width: 58px;
  height: 350px;
  position: absolute;
  top: 682px;
  right: calc((100% - 1190px) / 2 - 90px);
  background-color: #fff;
  font-size: 14px;
  .fff {
    color: #fff;
  }
  ul {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    li {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      padding: 10px;
      border-bottom: 1px solid lightgray;
      cursor: pointer;
    }
    .navliclass {
      background-color: #c81623;
      color: #fff !important;
    }
    li:last-child {
      border: none;
      display: none;
      color: #c81623;
    }
  }
}
</style>
