<template>
  <div class="foryoubar" ref="foryou">
    <div class="foryou-center w">
      <div class="foryou-title">
        <div class="foryou-bg1"></div>
        <h3>为你推荐</h3>
        <div class="foryou-bg2"></div>
      </div>
      <div class="foryou-nav">
        <div class="foryou-nav-center">
          <div>
            <a href="javascript:;">
              <h4>精选</h4>
              <div>猜你喜欢</div>
            </a>
          </div>
          <div>
            <a href="javascript:;">
              <h4>智能先锋</h4>
              <div>大电器城</div>
            </a>
          </div>
          <div>
            <a href="javascript:;">
              <h4>居家优品</h4>
              <div>品质生活</div>
            </a>
          </div>
          <div>
            <a href="javascript:;">
              <h4>超市百货</h4>
              <div>百货生鲜</div>
            </a>
          </div>
          <div>
            <a href="javascript:;">
              <h4>时尚达人</h4>
              <div>美妆穿搭</div>
            </a>
          </div>
          <div>
            <a href="javascript:;">
              <h4>进口好物</h4>
              <div>京东国际</div>
            </a>
          </div>
        </div>
      </div>
      <div class="foryou-main">
        <div v-for="(g, index) in goods" :key="g.id" ref="foryoubox">
          <a href="javascript:;" @click="runGoods(index)">
            <div class="foryou-img">
              <img :src="getPicture(index)" alt="" />
            </div>
            <p>
              {{ g.title }}
            </p>
            <span><i>￥</i>{{ g.price }}<i>00</i></span>
            <div class="foryou-x" @click="close(index)">×</div>
            <div class="findbox">
              <div class="find"><i class="fas fa-eye"></i>找相似</div>
            </div>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Foryoubar",
  data() {
    return {
      goods: null,
    };
  },
  methods: {
    getGoods() {
      this.axios.get("http://localhost:80/api/getItems.php").then(
        (res) => {
          this.goods = res.data;
          this.goods.forEach((g) => {
            g.picture = JSON.parse(g.picture);
            g.details = JSON.parse(g.details);
          });
        },
        (err) => {
          console.log(err);
        }
      );
    },
    getPicture(index){
      let reg = /img\/(.*).jpg$/;
      let str = this.goods[index].picture[0].src;
      let res = str.match(reg)[1];
      return require("../assets/img/"+res+".jpg")
    },
    close(index){
      this.$refs.foryoubox[index].style.display = 'none';
    },
    runGoods(index){
      this.$router.push({
        name:'shangpin',
        params:{
          good:this.goods[index]
        }
      })
    }
  },
  computed: {
    foryouTop() {
      return this.$refs.foryou.offsetTop;
    },
  },
  mounted() {
    this.$store.commit("getForyouTop", this.foryouTop);
    this.getGoods();
  },
};
</script>

<style lang="less">
.foryoubar {
  .foryou-center {
    .foryou-title {
      height: 45px;
      margin-bottom: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      .foryou-bg1 {
        width: 25px;
        height: 20px;
        background-image: url(../assets/img/square-sprite.png);
      }
      h3 {
        font-size: 28px;
        color: #333;
        margin: 0 20px;
        font-weight: 700;
      }
      .foryou-bg2 {
        width: 25px;
        height: 20px;
        background-image: url(../assets/img/square-sprite.png);
        background-position: -25px;
      }
    }
    .foryou-nav {
      height: 60px;
      margin-bottom: 10px;
      background-color: #fff;
      .foryou-nav-center {
        margin: 0 auto;
        height: 60px;
        width: 1080px;
        display: flex;
        align-items: center;
        > div {
          flex: 1;
          a {
            display: block;
            text-align: center;
            font-size: 14px;
            color: #999;
            text-decoration: none;
            h4 {
              font-size: 16px;
              color: #333;
              font-weight: 700;
              width: 70px;
              height: 27px;
              text-align: center;
              margin: 0 auto;
            }
          }
          a:hover h4,
          a:hover div {
            color: #c81623;
          }
        }
      }
    }
    .foryou-main {
      width: 1200px;
      display: flex;
      flex-wrap: wrap;
      > div {
        width: 230px;
        height: 322px;
        margin: 0 10px 10px 0;
        background-color: #fff;
        a {
          display: block;
          display: flex;
          flex-direction: column;
          width: 230px;
          height: 322px;
          text-decoration: none;
          position: relative;
          .foryou-img {
            width: 150px;
            height: 150px;
            margin: 30px auto 40px auto;
            img {
              width: 150px;
            }
          }
          p {
            align-self: center;
            width: 190px;
            height: 48px;
            font-size: 14px;
            line-height: 24px;
            color: #666;
            overflow: hidden;
            text-overflow: ellipsis;
            word-break: break-all;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
          > span {
            margin-top: 5px;
            margin-left: 20px;
            color: #e1251b;
            font-size: 18px;
            font-weight: 700;
            i {
              font-size: 12px;
            }
          }
          .foryou-x {
            width: 16px;
            height: 16px;
            text-align: center;
            line-height: 16px;
            position: absolute;
            top: 10px;
            right: 10px;
            color: #666;
            display: none;
          }
          .findbox {
            position: absolute;
            bottom: 8px;
            left: 0;
            width: 100%;
            height: 66px;
            display: none;
            .find {
              margin: auto;
              width: 120px;
              height: 32px;
              line-height: 32px;
              text-align: center;
              color: #fff;
              background-color: #333;
              border-radius: 15px;
            }
            .find:hover {
              background-color: #c81623;
              color: #fff;
            }
          }
        }
        a:hover img {
          opacity: 0.7;
        }
        a:hover .findbox,
        a:hover .foryou-x {
          display: block;
        }
        a:hover .findbox {
          background: -webkit-gradient(
            linear,
            left top,
            left bottom,
            from(rgba(255, 255, 255, 0.8)),
            color-stop(35%, white),
            to(white)
          );
        }
        .foryou-x:hover {
          background-color: #c81623;
          color: #fff;
        }
      }
    }
  }
}
</style>
