<template>
  <div ref="searchfix" class="searchfix">
    <div class="w">
      <div class="searchfix-logo"></div>
      <div class="head-searchbar">
        <div class="head-search">
          <input type="text" placeholder="请输入内容" /><a
            href="javascript:;"
            class="camera"
            ><i class="fas fa-camera"></i></a
          ><button class="btn"><i class="fas fa-search"></i></button>
        </div>
        <div class="head-shopcart">
          <i class="fas fa-shopping-cart"></i
          ><a href="javascript:;" @click="runShopcart">我的购物车</a>
          <i class="zero">{{ num }}</i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SearchFix",
  data() {
    return {
      num: 0,
    };
  },
  methods: {
    handleScroll() {
      if (
        document.documentElement.scrollTop >=
        this.$store.state.sekillTop - 41
      ) {
        this.$refs.searchfix.style.height = "50px";
        this.$refs.searchfix.style.borderBottom = "2px solid #e1251b";
      } else {
        this.$refs.searchfix.style.height = "0px";
        this.$refs.searchfix.style.borderBottom = "none";
      }
    },
    runShopcart() {
      if (this.num > 0) {
        this.$router.push({
          name: "gouwuche",
        });
      } else {
        alert("购物车内没有商品,请先添加商品");
      }
    },
  },
  created() {
    if (localStorage.getItem("goods2")) {
      this.num = JSON.parse(localStorage.getItem("goods2")).length;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

<style scoped lang="less">
.searchfix {
  height: 0px;
  background-color: #fff;
  position: fixed;
  z-index: 9999;
  width: 100%;
  top: 0;
  box-sizing: border-box;
  overflow: hidden;
  transition: height 0.3s;
  > div {
    display: flex;
    align-items: center;
    height: 100%;
    .searchfix-logo {
      background-image: url("../assets/img/searchlogo.png");
      background-position: 0 -120px;
      background-repeat: no-repeat;
      overflow: hidden;
      display: block;
      width: 125px;
      height: 40px;
      text-indent: -999px;
    }
    .head-searchbar {
      display: flex;
      margin-left: 130px;
      align-items: center;
      .head-search {
        width: 486px;
        height: 32px;
        border: 2px solid #e2231a;
        position: relative;
        display: flex;
        input {
          width: 367px;
          height: 26px;
          border: 1px solid transparent;
          padding: 2px 44px 2px 17px;
          outline: none;
          font-size: 12px;
        }
        .camera {
          display: block;
          position: absolute;
          top: 10px;
          left: 397px;
          color: #b1b1b1;
        }
        .btn {
          height: 34px;
          border: none;
          background-color: #e1251b;
          cursor: pointer;
          margin-right: -1px;
          margin-top: -1px;
          flex: 1;
          i {
            color: #fff;
            font-size: 20px;
          }
        }
        .camera:hover {
          color: #c81623;
        }
        .btn:hover {
          background-color: #c81623;
        }
      }
      .head-shopcart {
        width: 130px;
        height: 34px;
        line-height: 34px;
        border: 1px solid #eee;
        right: 230px;
        margin-left: 25px;
        text-align: center;
        position: relative;
        right: 0px;
        i {
          margin-right: 10px;
          font-size: 16px;
          color: #e1251b;
        }
        .zero {
          position: absolute;
          top: 1px;
          left: 29px;
          right: auto;
          display: inline-block;
          padding: 1px 3px;
          font-size: 12px;
          line-height: 12px;
          color: #fff;
          background-color: #e1251b;
          border-radius: 7px;
          min-width: 12px;
          text-align: center;
        }
        a {
          color: #e1251b;
          text-decoration: none;
          font-size: 12px;
        }
      }
      .head-shopcart:hover {
        border-color: #c81623;
      }
      .head-shopcart:hover a {
        color: #c81623;
      }
    }
  }
}
</style>
