<template>
  <div class="sekill-time">
    <a href="javascript:;">
      <div>京东秒杀</div>
      <div>
        <strong>{{ timeF }}</strong
        >点场&nbsp;&nbsp;距结束
      </div>
      <div class="sekill-time-main">
        <div class="timebox hour">{{ t.h }}</div>
        <div class="colon">:</div>
        <div class="timebox min">{{ t.m }}</div>
        <div class="colon">:</div>
        <div class="timebox sec">{{ t.s }}</div>
      </div>
    </a>
  </div>
</template>

<script>
export default {
  name: "SekillTime",
  data() {
    return {
      time: "2021-10-8 20:00:00",
      t: { h: "", m: "", s: "" },
    };
  },
  computed: {
    timeF() {
      let arr = this.time.split(" ");
      let arr2 = arr[1].split(":");
      return arr2[0] + ":" + arr2[1];
    },
  },
  mounted() {
    let t = new Date(this.time);
    let time1 = t.valueOf();
    let that = this;
    function countDown() {
      let time2 = Date.now();
      let time3 = time1 - time2;
      let alls = time3 / 1000;
      let h = parseInt((alls / 60 / 60) % 24); //时
      let m = parseInt((alls / 60) % 60); //分
      let s = parseInt(alls % 60); //秒
      that.t.h = h < 10 && h >= 0 ? "0" + h : h;
      that.t.m = m < 10 && m >= 0 ? "0" + m : m;
      that.t.s = s < 10 && s >= 0 ? "0" + s : s;
    }
    setInterval(countDown, 1000);
  },
};
</script>

<style lang="less">
.sekill-time {
  width: 190px;
  height: 100%;
  a {
    display: block;
    width: 100%;
    height: 100%;
    background-image: url(../assets/img/jdmiaoshabgi.png);
    text-decoration: none;
    color: #fff;
    > div:first-child {
      font-size: 30px;
      width: 100%;
      text-align: center;
      padding-top: 31px;
      font-weight: 700;
    }
    > div:nth-child(2) {
      font-size: 14px;
      margin-top: 98px;
      text-align: center;
      strong {
        font-size: 18px;
        padding-right: 2px;
        margin-top: -1px;
      }
    }
    .sekill-time-main {
      width: 100%;
      margin-top: 10px;
      display: flex;
      justify-content: center;
      .timebox {
        height: 30px;
        width: 30px;
        background-color: #2f3430;
        font-size: 20px;
        text-align: center;
        line-height: 30px;
      }
      .colon {
        height: 30px;
        width: 20px;
        font-size: 20px;
        text-align: center;
        line-height: 30px;
      }
    }
  }
}
</style>
